h2{
    
    .text-center &{
        display:inline-block;
    }
    main &:not(.card-title):after{
            content:'';
            height:.3rem;
            margin-top:1em;
            width:50%;
            display: block;
            @include multiple-border;
        } 
    .text-center &:after{
            height:.6rem;
            width:50%;
            margin:1em auto 0;
        } 
    }

.social-media-links{
    color:white;
    position: fixed;
    top:25vH;
    right:0;

    ul{
        margin:0;
        li{
            display:block;
            margin:0;
            text-align:center;
            line-height:0;
            width:3em;
            padding:.5em;
            &:nth-child(1), &:nth-child(5){
                background:$blue;
            }
            &:nth-child(2), &:nth-child(6){
                background:$yellow;
            }
            &:nth-child(3), &:nth-child(7){
                background:$pink;
            }
            &:nth-child(4), &:nth-child(8){
                background:$green;
            }
            a{
                font-size:2em;
                &:hover, &:focus{
                    text-decoration: none;
                    outline: none
                }

                span {
                    display: block;
                    text-indent: -99999px;
                }
            }
        }
    }
}
.cta{
    *{
        color:white;
    }
    .tab-content{
        margin-top:8px;
        background:$yellow;
        padding: 2em;
        @include shadow;
    }
    .nav-tabs{
        border:0;
        .nav-link{
            background:lighten($yellow,10%);
            border:0;
        }
        .nav-item.show .nav-link, .nav-link.active{
            background:$yellow;
            color:white;
        }
    }

}


/*** Content ***/
.list-group-item{
    border-width: 0 0 1px 0;
    margin: 0;
    padding: 1em .5em;
    border-color: currentColor;
    background: transparent;
    &:last-child{border:0;}
}
.list-group-item-action:hover, list-group-item-action:focus{
    footer &{
        color:white;
        background: darken($blue,10%);
    }
}
.card-body{
    padding:2em 2.4em;
}

.icon{

    padding: 0 2em .5em;
    font-size: 3em;

    .cat-yellow & {
        color: $blue;

    }
    .cat-yellow & {color: $yellow;}
    .cat-pink & {color: $pink;}
    .cat-green & {color: $green;}

    + a {
        min-height: 40px;
        display:block;
    }
}