/* =============================================================================
   Colors
   ========================================================================== */
$foo:red;
$blue:#159EE5;
$yellow:#FF9A34;
$pink:#FF77A5;
$green:#00B470;

$brand-primary:$blue;
$brand-secondary: $pink;

$brand-success:$green;
$brand-info:$yellow;
$brand-warning:#fcf8e3;
$brand-danger:#DB221C;

$body-bg:#FAFAFA;
$text-color:#333;
$light-text-color:#999;
$light-gray:#ccc;
$dark-gray:darken($light-gray,50%);
$link-color:$brand-primary;
$border-color:$light-gray;

$white: #fff;
$bg-inverse: #000;


/* =============================================================================
   Typography
   ========================================================================== */

$base-font-size:            100% !default;
$base-line-height:          150% !default;
$rem-base:                  14px !default;

$paragraph-margin:          1rem;

$headings-font-family:    'Lato', sans-serif;
$body-font-family:        'Muli', sans-serif;

$heading-text-color:        $text-color;
$body-text-color:           $text-color;
$text-muted-color:          lighten($text-color,25%);

/* Margins & Paddings */
$outer-grid:2 * $rem-base;
$vertical-spacing:2*$rem-base;